.contacts-container {
	background: #d6bbd3;
	width: 100%;
}

.contacts-box {
	width: 100%;
	max-width: 1215px;
	display: flex;
	justify-content: center;
	margin: 0 auto 75px;
	padding: 75px 0 75px 0;
	&__info-box {
		margin-bottom: 20px;
	}
	&__info {
		width: 304px;
		margin: 0 30px;
		&-title {
			font-family: "Roboto";
			font-style: normal;
			font-weight: 400;
			font-size: 14px;
			line-height: 30px;
			letter-spacing: 1.5px;
			text-transform: uppercase;
			color: #ffffff;
		}
		&-subtitle {
			font-family: "CormorantGaramond";
			font-style: normal;
			font-weight: 400;
			font-size: 22px;
			line-height: 30px;
			color: #ffffff;
			text-decoration: none;
		}
	}
	&__feedback {
		width: 600px;
		margin: 0 30px;
		&-text {
			margin-bottom: 20px;
		}
		&-form-1 {
			display: flex;
			justify-content: space-between;
			margin-bottom: 20px;
		}
		&-form-2 {
			display: flex;
			justify-content: space-between;
			margin-bottom: 20px;
		}
		&-form-3 {
			margin-bottom: 25px;
		}
		&-button {
			padding: 10px 70px;
			width: 210px;
			background-color: #d6bbd3;
			border: 1px solid white;
			font-family: "CormorantGaramond";
			font-style: normal;
			font-weight: 400;
			font-size: 18px;
			line-height: 30px;
			text-align: center;
			color: #ffffff;
		}
	}
}

::placeholder {
	color: #ffffff;
	padding: 10px 0 10px 15px;
}

.form-control {
	width: 288px;
	background-color: #d6bbd3;
	border: 1px solid white;
	font-family: "CormorantGaramond";
	font-style: italic;
	font-weight: 400;
	font-size: 18px;
	line-height: 30px;
	color: #ffffff;
}

#textarea {
	width: 600px;
	height: 100px;
	background-color: #d6bbd3;
	border: 1px solid white;
	font-family: "CormorantGaramond";
	font-style: italic;
	font-weight: 400;
	font-size: 18px;
	line-height: 30px;
	color: #ffffff;
}
