.header {
	margin: 30px auto 24px;
	max-width: 1440px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	&-bar {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&-menu {
		width: 507px;
	}
	&-button {
		padding-left: 45px;
	}
}

.menu-list {
	list-style-type: none;
	display: flex;
	justify-content: space-around;
	&-items-link {
		text-decoration: none;
		font-weight: 400;
		font-size: 24px;
		line-height: 30px;
		color: #b08ead;
	}
}
