.newsletter {
	&-box {
		text-align: center;
		margin: 0 auto 85px;
	}
	&-title {
		font-family: "Roboto";
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
		line-height: 30px;
		text-align: center;
		letter-spacing: 1.5px;
		text-transform: uppercase;
		color: #b99bb6;
		margin-bottom: 7px;
	}
	&-subtitle {
		font-family: "CormorantGaramond";
		font-style: normal;
		font-weight: 400;
		font-size: 24px;
		line-height: 30px;
		text-align: center;
		color: #5f4d5d;
		margin-bottom: 13px;
	}
	&-contacts {
		display: flex;
		justify-content: center;
	}
	&-button {
		width: 180px;
		border: 1px solid #ceb3cb;
		background: #ceb3cb;
		border-radius: 0px 3px 3px 0px;
		color: #fff;
		padding: 15px 44px;
	}
	&-form {
		width: 208px;
		background: #ffffff;
		border: 1px solid #ceb3cb;
		border-radius: 3px 0px 0px 3px;
		padding: 15px 0px 15px 0;
		&::placeholder {
			font-family: "Cormorant Garamond";
			font-style: italic;
			font-weight: 400;
			font-size: 18px;
			line-height: 30px;
			text-align: center;
			color: #ceb3cb;
		}
	}
}
