.shop {
	&-box {
		display: flex;
		justify-content: space-between;
		flex-flow: row wrap;
		max-width: 1215px;
		height: 990px;
		margin: 0 auto 80px;
		width: 100%;
	}
	&-item {
		width: 377px;
		height: 446px;
		text-align: center;
	}
}

.item {
	&-img {
		margin-bottom: 9px;
	}
	&-name {
		font-family: "CormorantGaramond";
		font-style: normal;
		font-weight: 400;
		font-size: 24px;
		line-height: 30px;
		text-align: center;
		color: #5f4d5d;
		margin-bottom: 7px;
	}
	&-price {
		font-family: "Roboto";
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		line-height: 18px;
		text-align: center;
		color: #b0b0b0;
	}
}
