.footer {
	background: #c9adc6;
	padding: 40px 0;
	&-container {
		width: 100%;
		max-width: 1265px;
		margin: 0 auto;
	}
	&-nav {
		display: flex;
		justify-content: start;
		align-items: center;
	}
	&-logo {
		margin-right: 65px;
	}
	&-links {
		text-align: center;
		margin-right: 500px;
		&_item {
			text-decoration: none;
			font-family: "CormorantGaramond";
			font-style: normal;
			font-weight: 400;
			font-size: 21px;
			line-height: 21px;
			color: #ffffff;
			margin-right: 35px;
		}
	}
}
