.reverse {
	flex-direction: row-reverse;
	justify-content: flex-end;
	padding-left: 101px;
	& .info-container {
		margin-left: 36px;
		margin-right: -27px;
	}
	& .img-box {
		z-index: 1;
	}
}

.info-container {
	&_uppertitle {
		font-size: 14px;
		line-height: 30px;
		letter-spacing: 1.5px;
		text-transform: uppercase;
		color: #ffffff;
		margin-bottom: 10px;
	}
	&_title {
		font-size: 40px;
		line-height: 55px;
		color: #ffffff;
		letter-spacing: -0.6px;
		margin-bottom: 22px;
	}
	&_link {
		font-size: 14px;
		line-height: 30px;
		letter-spacing: 1.5px;
		text-transform: uppercase;
		color: #ffffff;
		text-decoration: none;
		position: relative;
	}
	&_link::after {
		content: "";
		position: absolute;
		top: calc(100% + 10px);
		background: white;
		left: 0;
		width: 0%;
		height: 2px;
		transition: 0.3s;
	}
	&_link:hover::after {
		width: 100%;
	}
}

.img-z-index .img-box {
	z-index: 1;
}

.img-z-index .info-container {
	margin-left: -33px;
	padding: 124px 104px 124px 50px;
}

.img-fluid {
	max-width: 100%;
	height: 100%;
}
