.about {
	display: flex;
	flex-direction: column;
	width: 1265px;
	height: 1500px;
	margin: 0 auto 75px;
	padding: 0 25px;
	&-me {
		display: flex;
		flex-direction: row;
		width: 1215px;
		height: 457px;
		position: relative;
		padding-bottom: 37px;
		margin-bottom: 40px;
		&.about-reverse::before {
			content: "";
			width: 1215px;
			height: 1px;
			background-color: #d7b7d4;
			display: flex;
			position: absolute;
			top: calc(0% - 7.3%);
		}
		&.about-reverse::after {
			content: "";
			width: 1215px;
			height: 1px;
			background-color: #d7b7d4;
			display: flex;
			position: absolute;
			top: 100%;
		}
		&__info {
			padding: 88px 60px;
		}
		&__title {
			font-family: "Roboto";
			font-style: normal;
			font-weight: 400;
			font-size: 14px;
			line-height: 30px;
			letter-spacing: 1.5px;
			text-transform: uppercase;
			color: #b99bb6;
			margin-bottom: 10px;
		}
		&__subtitle {
			font-family: "CormorantGaramond";
			font-style: normal;
			font-weight: 400;
			font-size: 24px;
			line-height: 30px;
			color: #5f4d5d;
			margin-bottom: 10px;
		}
		&_img {
			padding-left: 37px;
		}
	}
	&-reverse {
		flex-direction: row-reverse;
		height: 547px;
	}
}

.box-info_button__carrer {
	width: 141px;
	height: 45px;
	border: 1px solid #b99bb6;
	border-radius: 25px;
	margin-top: 20px;
}
