.banner-template {
	margin-bottom: 75px;
	padding: 69px 0 70px;
	background: linear-gradient(to bottom, #c9adc6 57%, #d6bbd3 43%);
	&__container {
		max-width: 1215px;
		padding-left: 65px;
		margin: 0 auto;
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}
}

.has-decoration .banner-template__container {
	position: relative;
	&::before {
		content: "";
		font-size: 72px;
		width: 50px;
		height: 50px;
		background: #a78ba4;
		display: flex;
		position: absolute;
		top: calc(100% + 20px);
		right: 50px;
	}
	&::after {
		content: "";
		font-size: 72px;
		width: 50px;
		height: 50px;
		background: #b08ead;
		display: flex;
		position: absolute;
		top: calc(100% + 20px);
		right: 0;
	}
}

.info-container {
	padding: 124px 104px 124px 40px;
	background-color: #b08ead;
	min-width: 425px;
	width: 425px;
	margin-left: -126px;
}
