.services {
	&-container {
		background: #d6bbd3;
		width: 100%;
		text-align: center;
		padding: 46px 0 35px;
	}
	&-box {
		justify-content: space-evenly;
		display: flex;
		width: 100%;
		max-width: 1215px;
		margin: 0 auto;
	}
	&-title {
		font-family: "Roboto";
		font-style: normal;
		font-weight: 400;
		font-size: 17px;
		line-height: 30px;
		text-align: center;
		color: #ffffff;
	}
	&-subtitle {
		font-family: "CormorantGaramond";
		font-style: normal;
		font-weight: 400;
		font-size: 21px;
		line-height: 30px;
		text-align: center;
		color: #ffffff;
	}
}
