.home-details {
	max-width: 1440px;
	margin: 0 auto 75px;
	&_box-cards {
		width: 1186px;
		margin: 0 auto 75px;
		display: flex;
		justify-content: space-between;
	}
}

.box-info {
	&_title {
		font-family: "Roboto";
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
		line-height: 30px;
		text-align: center;
		letter-spacing: 1.5px;
		color: #b99bb6;
		margin: 0 auto 13px;
	}
	&_subtitle {
		width: 789px;
		font-family: "CormorantGaramond";
		font-style: normal;
		font-weight: 400;
		font-size: 26px;
		line-height: 38px;
		text-align: center;
		color: #5f4d5d;
		margin: 0 auto 17px;
	}
	&_button {
		width: 141px;
		height: 45px;
		border: 1px solid #b99bb6;
		border-radius: 25px;
		margin: 0 auto 50px;
		text-align: center;
		&-title {
			margin: 16px auto 14px;
			font-family: "Roboto";
			font-style: normal;
			font-weight: 400;
			font-size: 11px;
			line-height: 15px;
			text-align: center;
			letter-spacing: 1.5px;
			color: #b99bb6;
		}
	}
}

.box-card {
	height: 427px;
	background-color: #ceb3cb;
	&_title {
		font-family: "CormorantGaramond";
		font-style: normal;
		font-weight: 400;
		font-size: 25px;
		color: #ffffff;
		margin-bottom: 6px;
		padding-left: 23px;
	}
	&_link {
		font-family: "CormorantGaramond";
		text-decoration: none;
		font-size: 17px;
		line-height: 30px;
		color: #ffffff;
		padding-left: 23px;
	}
}

.card_about-me_photo {
	margin-bottom: 23px;
}
